html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after , *::before {
  box-sizing: inherit;
  -webkit-user-select: none;
  user-select: none;
}

body{
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}