  
// 640px - 1024px(1150px) - 1400px
$breakpoints-up:(
  'medium':'40em',
  // 'x':'54.75em',
  'large':'71.875em',
  'xlarge':'87.5em',
);


// 639px - 1023px(1149px) - 1339px
$breakpoints-down:(
  'small':'39.9375em',
  'medium':'71.8125em',
  'large':'87.4375em',
);

@mixin breakpoint($size) {
  @media (min-width: map-get($breakpoints-up , $size)) {
    @content;
  }
}


@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down , $size)) {
    @content;
  }
}