:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --facebook-shadow: hsla(208, 92%, 53%, 0.1);
  --facebook-shadow-hover: hsla(208, 92%, 53%, 0.8);
  --twitter: hsl(203, 89%, 53%);
  --twitter-shadow: hsla(203, 89%, 53%, 0.1);
  --twitter-shadow-hover: hsla(203, 89%, 53%, 0.8);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-start-shadow: hsla(37, 97%, 70%, 0.1);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --instagram-end-shadow: hsla(329, 70%, 58%, 0.8);
  --youtube: hsl(348, 97%, 39%);
  --youtube-shadow: hsla(348, 97%, 39%, 0.1);
  --youtube-shadow-hover: hsla(348, 97%, 39%, 0.8);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: #343b56;
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
  -webkit-user-select: none;
  user-select: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a, a:active, a:visited {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  border: none;
  gap: 0.3rem 0.5rem;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
  justify-self: start;
}
.toggle__wrapper {
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: nowrap;
  max-width: 3.75rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
  width: 33.3333333333%;
  min-width: 1.25rem;
  height: 100%;
  border-radius: 0;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0rem;
  overflow: hidden;
  border-radius: 0.75rem;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  pointer-events: none;
}
.toggle__button {
  width: 1.125rem;
  height: 1.125rem;
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 0.1875rem;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  transform: translate(-50%);
  right: auto;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  padding: 1.5rem;
  background: var(--dark-card);
  color: var(--dark-text1);
  text-align: center;
  border-radius: 0.3125rem;
  flex: 1;
  overflow: hidden;
  cursor: pointer;
  transition: all 200ms ease-in-out;
}
.card:hover {
  background: var(--dark-card-hover);
  transform: scale(1.08);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
  transition: all 400ms ease-in-out;
}
.card--facebook, .card--facebook__grid {
  box-shadow: 0rem 0rem 0.5rem 0.125rem var(--facebook-shadow);
}
.card--facebook:hover, .card--facebook__grid:hover {
  box-shadow: 0rem 0rem 1rem 0.125rem var(--facebook-shadow-hover);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
  transition: all 400ms ease-in-out;
}
.card--twitter, .card--twitter__grid {
  box-shadow: 0rem 0rem 0.5rem 0.125rem var(--twitter-shadow);
}
.card--twitter:hover, .card--twitter__grid:hover {
  box-shadow: 0rem 0rem 1rem 0.125rem var(--twitter-shadow-hover);
}
.card--instagram {
  padding-top: 1.875rem;
  position: relative;
  transition: all 400ms ease-in-out;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  border-top-left-radius: 0.3125rem;
  border-top-right-radius: 0.3125rem;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card--instagram, .card--instagram__grid {
  box-shadow: 0rem 0rem 0.5rem 0.125rem var(--instagram-start-shadow);
}
.card--instagram:hover, .card--instagram__grid:hover {
  box-shadow: 0rem 0rem 0.5rem 0.125rem var(--instagram-end-shadow);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
  transition: all 400ms ease-in-out;
}
.card--youtube, .card--youtube__grid {
  box-shadow: 0rem 0rem 0.5rem 0.125rem var(--youtube-shadow);
}
.card--youtube:hover, .card--youtube__grid:hover {
  box-shadow: 0rem 0rem 1rem 0.125rem var(--youtube-shadow-hover);
}
.card--grid {
  text-align: initial;
  display: grid !important;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 1.25rem 0;
  justify-items: start;
}
.card--grid > .card__username {
  align-self: center;
}
.card--grid > img, .card--grid > .card__change {
  justify-self: end;
}
.card--grid > .card__change, .card--grid > .span {
  align-self: end;
}
.card--grid > .card__change {
  padding-bottom: 0.375rem;
}
.card__platform {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  height: 1.25rem;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5rem;
}
.card__followers__count {
  color: var(--light-bg);
}
.card__followers__count--big {
  font-size: 3.5rem;
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.375rem;
}
.card__followers__count--small {
  font-size: 2rem;
  font-weight: 700;
}
.card__followers__label {
  color: var(--dark-text1);
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: normal;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change--number {
  font-size: 0.75rem;
  font-weight: 700;
}